// INCIDENT

.incident-col {
  padding: 0;
}

.incident-item {

  .incident-img-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 75%;

      padding: 8px;

      border-radius: 50%;
      background-color: lightgray;
    }
  }

  .incident-desc {
    color: black;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    word-break: break-word;

    margin: 6px;
  }
}
